.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 3px 10px #0000000d;
}

.header__logo img {
  width: 40px;
  height: 40px;
  display: flex;
}

.nav__list {
  column-gap: 20px;
  list-style: none;
  display: flex;
}

.nav__link {
  text-transform: uppercase;
  color: #060b35;
  font-size: 12px;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  display: inline-block;
  position: relative;
}

.nav__link:hover {
  color: #00acdc;
  transition-duration: .3s;
}

.is-active {
  color: #00acdc;
}

.is-active:after {
  content: "";
  opacity: 1;
  background-color: #00acdc;
  border-radius: 9px;
  width: 100%;
  height: 5px;
  position: absolute;
  bottom: -1px;
  left: 0;
}

.stars {
  flex-wrap: wrap;
  align-content: center;
  display: flex;
}

.stars__star {
  background: url("star.822d561e.svg") center no-repeat;
  flex-wrap: wrap;
  width: 16px;
  height: 16px;
  margin-right: 4px;
  display: flex;
}

.stars--1 .stars__star:nth-child(-n+1), .stars--2 .stars__star:nth-child(-n+2), .stars--3 .stars__star:nth-child(-n+3), .stars--4 .stars__star:nth-child(-n+4), .stars--5 .stars__star:nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

.cards {
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

.card {
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  margin-bottom: 0;
}

.card > img {
  width: 160px;
  margin: 32px auto 0;
  display: block;
}

.card__details {
  margin: 40px 16px 0;
}

.card__details-title {
  color: #060b35;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__details-code {
  color: #616070;
  margin-top: 4px;
  font-size: 10px;
  line-height: 14px;
}

.card__details:hover {
  color: #34568b;
  transition-duration: .3s;
}

.card__price {
  justify-content: space-between;
  margin: 0 16px;
  display: flex;
}

.card__price-text {
  color: #616070;
  font-size: 12px;
  line-height: 18px;
}

.card__price-number {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button a {
  color: #fff;
  text-transform: uppercase;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
}

.card__button a:hover {
  color: #00acdc;
  transition-duration: .3s;
}

.card__button {
  background-color: #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 100%;
  max-width: 166px;
  margin: 16px;
  padding: 12px 20px;
  display: flex;
}

.card__button:hover {
  background-color: #fff;
  border: 1px solid #00acdc;
  transition-duration: .3s;
}

.card__button:hover a {
  color: #00acdc;
}

.card__stars {
  justify-content: space-between;
  margin: 17px 16px 25px;
  display: flex;
}

.card__stars-review {
  color: #060b35;
  font-size: 10px;
  line-height: 14px;
}

@media screen and (width >= 768px) {
  .cards {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media screen and (width >= 1024px) {
  .cards {
    grid-gap: 46px 48px;
    grid-template-columns: repeat(4, 200px);
    padding: 50px 40px;
  }
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
}

